import React, { useState, useEffect, useRef } from 'react';
import { Link } from 'react-router-dom';

// Simple feedback practice component
const Feedback: React.FC = () => {
  const [currentScenario, setCurrentScenario] = useState(0);
  const [selectedOption, setSelectedOption] = useState<string | null>(null);
  const [showFeedback, setShowFeedback] = useState(false);
  const [score, setScore] = useState(0);
  const [isComplete, setIsComplete] = useState(false);
  const [showExplanation, setShowExplanation] = useState(false);
  const [feedbackHistory, setFeedbackHistory] = useState<Array<{
    scenario: string;
    choice: string;
    wasCorrect: boolean;
    explanation: string;
  }>>([]);

  const feedbackRef = useRef<HTMLDivElement>(null);

  // Scroll to top when component mounts
  useEffect(() => {
    window.scrollTo({ top: 0, behavior: 'smooth' });
  }, []);

  // Simplified scenarios with clear options
  const scenarios = [
    {
      situation: "Junior vvoj opakovan odevzdv kd bez unit test",
      context: "Mte v tmu juniora, kter je technicky zdatn a pe dobr kd. Nicmn i pes pedchoz upozornn stle neodevzdv unit testy ke svmu kdu, co komplikuje review process a zvyuje riziko bug v produkci.",
      image: "https://images.unsplash.com/photo-1557804506-669a67965ba0?ixlib=rb-4.0.3&auto=format&fit=crop&w=1374&q=80",
      options: [
        {
          id: "1a",
          text: "Tohle u je ponkolikt. Pt bez test kd nepijmu do review.",
          type: "directive",
          feedback: "Tento pstup je pli konfrontan a me vyvolat odpor. Navc nepomh juniorovi pochopit hodnotu testovn.",
          explanation: "Efektivn zptn vazba by mla zahrnovat vysvtlen PRO jsou testy dleit a nabdnout podporu pi jejich implementaci.",
          isCorrect: false
        },
        {
          id: "1b",
          text: "Viml jsem si, e kd stle neobsahuje testy. Meme si spolen projt, jak testy pst a jakou pidvaj hodnotu? Rd ti s tm pomohu.",
          type: "constructive",
          feedback: "Vborn! Nabzte podporu a vzdlvn msto kritiky.",
          explanation: "Tento pstup je efektivn, protoe:\n1. Uznvte problm konkrtn\n2. Nabzte een a podporu\n3. Vytvte pleitost pro uen\n4. Zachovvte pozitivn vztah",
          isCorrect: true
        },
        {
          id: "1c",
          text: "Pt prosm nezapome na testy, jsou dleit.",
          type: "vague",
          feedback: "Tato zptn vazba je pli vgn a neposkytuje konkrtn veden nebo podporu.",
          explanation: "Chyb zde:\n1. Konkrtn dvody pro jsou testy dleit\n2. Nabdka pomoci\n3. Pleitost k diskuzi\n4. Konstruktivn een situace",
          isCorrect: false
        }
      ],
      bestPractice: "Pi poskytovn zptn vazby juniorm je klov kombinovat jasn oekvn s podporou a mentoringem. Vysvtlen 'pro' je stejn dleit jako 'co' a 'jak'."
    },
    {
      situation: "Seniorn kolega m konfliktn komunikaci v code reviews",
      context: "V seniorn vvoj m hlubok technick znalosti, ale jeho komente v code reviews jsou asto sarkastick a nkdy a agresivn. Nkolik len tmu se ji zmnilo, e se boj commitovat kd kvli jeho reakcm.",
      image: "https://images.unsplash.com/photo-1522071820081-009f0129c71c?ixlib=rb-4.0.3&auto=format&fit=crop&w=1470&q=80",
      options: [
        {
          id: "2a",
          text: "Vae technick znalosti jsou pro tm velmi cenn, ale zpsob, jakm komunikujete v code reviews, m negativn dopad na tm. Meme probrat, jak poskytovat konstruktivnj feedback?",
          type: "constructive",
          feedback: "Vborn! Uznvte hodnotu kolegy a zrove adresujete problm pmo a konstruktivn.",
          explanation: "Tento pstup je efektivn, protoe:\n1. Zante pozitivnm uznnm\n2. Jasn pojmenovvte problm\n3. Vysvtlujete dopad na tm\n4. Nabzte spolen hledn een",
          isCorrect: true
        },
        {
          id: "2b",
          text: "Tm si stuje na tvoje code reviews. Mus zmnit svj pstup.",
          type: "directive",
          feedback: "Tento pstup je pli konfrontan a me vst k defenzivn reakci.",
          explanation: "Chyb zde:\n1. Uznn pozitivnch pnos\n2. Konkrtn pklady problmu\n3. Vysvtlen dopadu\n4. Konstruktivn nvrh een",
          isCorrect: false
        },
        {
          id: "2c",
          text: "Mohl bys bt prosm v code reviews mrnj? Lid se boj commitovat.",
          type: "vague",
          feedback: "Tato zptn vazba je pli mkk a neadresuje jdro problmu.",
          explanation: "Tento pstup nen optimln, protoe:\n1. Nespecifikuje konkrtn problmov chovn\n2. Neposkytuje jasn oekvn\n3. Nezmiuje hodnotu technickch znalost\n4. Me bt snadno odmtnut",
          isCorrect: false
        }
      ],
      bestPractice: "Pi poskytovn zptn vazby seniornm kolegm je dleit uznat jejich expertizu a pnos, ale zrove jasn komunikovat dopady jejich chovn na tm."
    },
    {
      situation: "len tmu pravideln chod pozd na stand-upy",
      context: "Jeden z vaich vvoj opakovan chod pozd na rann stand-upy, co naruuje plynulost meeting a zdruje ostatn leny tmu. Jeho prce je jinak kvalitn a termny pln vas.",
      image: "https://images.unsplash.com/photo-1600880292203-757bb62b4baf?ixlib=rb-4.0.3&auto=format&fit=crop&w=1470&q=80",
      options: [
        {
          id: "3a",
          text: "Vmm si, e asto chod pozd na stand-upy. Oceuji kvalitu tv prce, ale pozdn pchody naruuj plynulost meeting. Co ti brn pijt vas?",
          type: "constructive",
          feedback: "Vborn! Kombinujete uznn pozitivnho s adresovnm problmu a hlednm pin.",
          explanation: "Tento pstup je efektivn, protoe:\n1. Konkrtn pojmenovvte problm\n2. Uznvte siln strnky\n3. Vysvtlujete dopad na tm\n4. Ptte se na piny a hledte een",
          isCorrect: true
        },
        {
          id: "3b",
          text: "Bu prosm pt vas na stand-upu.",
          type: "directive",
          feedback: "Tato zptn vazba je pli stroh a neposkytuje prostor pro dialog.",
          explanation: "Chyb zde:\n1. Uznn dobr prce\n2. Vysvtlen dopadu na tm\n3. Snaha pochopit piny\n4. Prostor pro spolen een",
          isCorrect: false
        },
        {
          id: "3c",
          text: "Vm, e odvd skvlou prci, ale ml bys chodit vas na meetingy.",
          type: "sandwich",
          feedback: "Sendviov metoda zde nen optimln, protoe nee piny problmu.",
          explanation: "Tento pstup nen nejlep, protoe:\n1. Zmkuje dleitost problmu\n2. Neposkytuje konkrtn feedback\n3. Neotevr prostor pro dialog\n4. Nee skuten piny",
          isCorrect: false
        }
      ],
      bestPractice: "Efektivn zptn vazba by mla bt konkrtn, vyven a zamen na hledn een. Je dleit uznat siln strnky, ale zrove jasn komunikovat problmy a jejich dopady."
    }
  ];

  const handleOptionSelect = (optionId: string) => {
    if (showFeedback) return;
    
    const selectedOptionData = scenarios[currentScenario].options.find(
      (option) => option.id === optionId
    );

    if (!selectedOptionData) return;

    setSelectedOption(optionId);
    setShowFeedback(true);

    setFeedbackHistory(prev => [...prev, {
      scenario: scenarios[currentScenario].situation,
      choice: selectedOptionData.text,
      wasCorrect: selectedOptionData.isCorrect,
      explanation: selectedOptionData.explanation
    }]);

    if (selectedOptionData.isCorrect) {
      setScore(prev => prev + 1);
    }

    // Scroll to feedback after a short delay to ensure the content is rendered
    setTimeout(() => {
      feedbackRef.current?.scrollIntoView({ behavior: 'smooth', block: 'nearest' });
    }, 100);
  };

  const handleNextScenario = () => {
    if (currentScenario < scenarios.length - 1) {
      setCurrentScenario(prev => prev + 1);
      setSelectedOption(null);
      setShowFeedback(false);
      setShowExplanation(false);
      // Scroll to top of the page
      window.scrollTo({ top: 0, behavior: 'smooth' });
    } else {
      setIsComplete(true);
    }
  };

  const resetExercise = () => {
    setCurrentScenario(0);
    setSelectedOption(null);
    setShowFeedback(false);
    setScore(0);
    setIsComplete(false);
    setFeedbackHistory([]);
    setShowExplanation(false);
  };

  const getFeedbackTypeIcon = (type: string) => {
    switch (type) {
      case 'constructive': return '';
      case 'directive': return '';
      case 'sandwich': return '';
      case 'vague': return '';
      default: return '';
    }
  };

  const getFeedbackTypeLabel = (type: string) => {
    switch (type) {
      case 'constructive': return 'Konstruktivn zptn vazba';
      case 'directive': return 'Direktivn pstup';
      case 'sandwich': return 'Sendviov metoda';
      case 'vague': return 'Vgn zptn vazba';
      default: return '';
    }
  };

  const getFeedbackTypeColor = (type: string) => {
    switch (type) {
      case 'constructive': return 'bg-green-100 text-green-800';
      case 'directive': return 'bg-red-100 text-red-800';
      case 'sandwich': return 'bg-yellow-100 text-yellow-800';
      case 'vague': return 'bg-gray-100 text-gray-800';
      default: return '';
    }
  };

  const getScoreMessage = () => {
    const percentage = (score / scenarios.length) * 100;
    
    if (percentage >= 80) {
      return {
        title: 'Vborn!',
        message: 'Mte skvl porozumn principm efektivn zptn vazby.',
        icon: '',
        color: 'text-green-600'
      };
    } else if (percentage >= 50) {
      return {
        title: 'Dobr zklad!',
        message: 'Rozumte zkladnm principm zptn vazby, ale je zde prostor pro zlepen.',
        icon: '',
        color: 'text-yellow-600'
      };
    } else {
      return {
        title: 'Prostor pro zlepen',
        message: 'Zkuste se zamit na konkrtnost a konstruktivnost ve va zptn vazb.',
        icon: '',
        color: 'text-red-600'
      };
    }
  };

  const currentScenarioData = scenarios[currentScenario];

  return (
    <div className="flex flex-col items-center justify-center min-h-screen bg-[#F5F6F7] p-6">
      <div className="card max-w-4xl w-full">
        <div className="flex justify-between items-center mb-6">
          <Link to="/exercises" className="text-[#321C3D] hover:text-[#18B48E] transition-colors flex items-center">
            <svg className="w-5 h-5 mr-2" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M10 19l-7-7m0 0l7-7m-7 7h18" />
            </svg>
            Zpt na procviovn
          </Link>
        </div>
        
        {!isComplete ? (
          <>
            <div className="flex justify-between items-center mb-4">
              <h1 className="text-3xl font-bold text-[#321C3D]">Poskytovn zptn vazby</h1>
              <div className="text-sm bg-gray-200 px-3 py-1 rounded-full">
                {currentScenario + 1} / {scenarios.length}
              </div>
            </div>
            
            <div className="relative h-48 mb-6 rounded-xl overflow-hidden">
              <img 
                src={currentScenarioData.image} 
                alt={currentScenarioData.situation} 
                className="w-full h-full object-cover"
              />
              <div className="absolute inset-0 bg-gradient-to-t from-black/70 to-transparent flex items-end">
                <h2 className="text-xl font-bold text-white p-4">{currentScenarioData.situation}</h2>
              </div>
            </div>
            
            <div className="bg-[#321C3D] text-white p-4 rounded-xl mb-6">
              <div className="text-lg font-medium mb-2">Situace:</div>
              <p className="text-white/90">{currentScenarioData.context}</p>
            </div>
            
            <div className="mb-8">
              <h3 className="text-lg font-medium mb-4">Vyberte nejefektivnj zpsob poskytnut zptn vazby:</h3>
              
              <div className="space-y-4">
                {currentScenarioData.options.map(option => (
                  <div 
                    key={option.id}
                    className={`p-4 border-2 rounded-xl cursor-pointer transition-all ${
                      selectedOption === option.id 
                        ? option.isCorrect 
                          ? 'border-green-500 bg-green-50' 
                          : 'border-red-500 bg-red-50'
                        : 'border-gray-200 hover:border-[#18B48E] hover:shadow'
                    }`}
                    onClick={() => handleOptionSelect(option.id)}
                  >
                    <p className="text-gray-800">{option.text}</p>
                  </div>
                ))}
              </div>
            </div>
            
            {showFeedback && (
              <div ref={feedbackRef} className="mt-6 space-y-4">
                <div className={`p-4 rounded-lg ${
                  selectedOption && scenarios[currentScenario].options.find(o => o.id === selectedOption)?.isCorrect
                    ? 'bg-green-100'
                    : 'bg-yellow-100'
                }`}>
                  <div className="flex items-start">
                    <div className="flex-shrink-0">
                      {selectedOption && scenarios[currentScenario].options.find(o => o.id === selectedOption)?.isCorrect ? (
                        <svg className="h-5 w-5 text-green-400" viewBox="0 0 20 20" fill="currentColor">
                          <path fillRule="evenodd" d="M10 18a8 8 0 100-16 8 8 0 000 16zm3.707-9.293a1 1 0 00-1.414-1.414L9 10.586 7.707 9.293a1 1 0 00-1.414 1.414l2 2a1 1 0 001.414 0l4-4z" clipRule="evenodd" />
                        </svg>
                      ) : (
                        <svg className="h-5 w-5 text-yellow-400" viewBox="0 0 20 20" fill="currentColor">
                          <path fillRule="evenodd" d="M18 10a8 8 0 11-16 0 8 8 0 0116 0zm-7-4a1 1 0 11-2 0 1 1 0 012 0zM9 9a1 1 0 000 2v3a1 1 0 001 1h1a1 1 0 100-2v-3a1 1 0 00-1-1H9z" clipRule="evenodd" />
                        </svg>
                      )}
                    </div>
                    <div className="ml-3">
                      <p className={`font-medium mb-2 ${
                        selectedOption && scenarios[currentScenario].options.find(o => o.id === selectedOption)?.isCorrect
                          ? 'text-green-800'
                          : 'text-yellow-800'
                      }`}>
                        {selectedOption && scenarios[currentScenario].options.find(o => o.id === selectedOption)?.feedback}
                      </p>
                      <div className="text-sm whitespace-pre-line text-gray-700">
                        {selectedOption && scenarios[currentScenario].options.find(o => o.id === selectedOption)?.explanation}
                      </div>
                    </div>
                  </div>
                </div>

                <div className="bg-blue-50 border-l-4 border-blue-500 p-4 rounded-r-lg">
                  <h4 className="font-bold text-blue-800 mb-2">Nejlep praxe:</h4>
                  <p className="text-blue-800">{currentScenarioData.bestPractice}</p>
                </div>

                <div className="flex justify-end mt-4">
                  <button 
                    className="px-6 py-3 bg-[#18B48E] text-white rounded-lg hover:bg-[#18B48E]/90 transition-colors"
                    onClick={handleNextScenario}
                  >
                    {currentScenario < scenarios.length - 1 ? 'Dal situace' : 'Zobrazit vsledky'}
                  </button>
                </div>
              </div>
            )}
          </>
        ) : (
          <div>
            <div className={`p-6 rounded-xl mb-6 bg-opacity-10 ${getScoreMessage().color}`}>
              <div className="flex items-center mb-6">
                <div className="text-5xl mr-6">{getScoreMessage().icon}</div>
                <div>
                  <h2 className={`text-3xl font-bold mb-2 ${getScoreMessage().color}`}>
                    {getScoreMessage().title}
                  </h2>
                  <p className="text-gray-700">{getScoreMessage().message}</p>
                </div>
              </div>

              <div className="bg-white rounded-lg p-6 mb-6">
                <h3 className="text-xl font-bold mb-4">Pehled vaich odpovd</h3>
                <div className="space-y-4">
                  {feedbackHistory.map((entry, index) => (
                    <div key={index} className={`p-4 rounded-lg ${
                      entry.wasCorrect ? 'bg-green-50' : 'bg-red-50'
                    }`}>
                      <div className="font-medium mb-2">{entry.scenario}</div>
                      <p className="text-sm mb-2">Vae odpov:</p>
                      <p className="text-gray-700 mb-3 pl-4 border-l-2 border-gray-300">
                        {entry.choice}
                      </p>
                      <div className={`text-sm ${
                        entry.wasCorrect ? 'text-green-700' : 'text-red-700'
                      }`}>
                        {entry.explanation}
                      </div>
                    </div>
                  ))}
                </div>
              </div>
            </div>

            <div className="bg-blue-50 border-l-4 border-blue-500 p-4 rounded-r-lg mb-6">
              <h4 className="font-bold text-blue-800 mb-2">Klov principy efektivn zptn vazby:</h4>
              <ul className="list-disc list-inside text-blue-800 space-y-1">
                <li>Bute konkrtn - popite situaci a jej dopad</li>
                <li>Zamte se na chovn, ne na osobnost</li>
                <li>Nabdnte konstruktivn een a podporu</li>
                <li>Vytvote bezpen prostor pro dialog</li>
                <li>Poskytujte zptnou vazbu vas a v soukrom</li>
                <li>Vysvtlete dvody a kontext</li>
                <li>Bute empatit a profesionln</li>
              </ul>
            </div>
            
            <div className="flex justify-between">
              <button 
                className="px-6 py-3 bg-white text-[#18B48E] border border-[#18B48E] rounded-lg hover:bg-[#18B48E]/5 transition-colors"
                onClick={resetExercise}
              >
                Zkusit znovu
              </button>
              
              <Link 
                to="/exercises" 
                className="px-6 py-3 bg-[#18B48E] text-white rounded-lg hover:bg-[#18B48E]/90 transition-colors"
              >
                Dokonit cvien
              </Link>
            </div>
          </div>
        )}
      </div>
    </div>
  );
};

export default Feedback; 